.card-img{
    height: 90vh;
    opacity: 0.6;
    /* width: 100vh; */
}
.card-img-overlay{
  font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 3.5vmin;
    margin-inline: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-weight: bold;
    color: #092b12;
    

}
.card-img-top{
    height: 50vh;
    opacity: 0.6;
}
.card-body{
  font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 2.5vmin;
    margin-inline: 0.5rem;
    margin-block: 0.5rem;
    color: #092b12;
}

body {
    --text-color: white;
    /* Your other styles */
  }
  
  body.dark-mode ,
  .dark-mode .card-body ,
  .dark-mode .card{
    background-color: #222;
  }

  body.dark-mode a {
    color: var(--text-color);
  }
  
  body.dark-mode h1,
  body.dark-mode h2,
  body.dark-mode h3,
  body.dark-mode h4,
  body.dark-mode h5,
  body.dark-mode h6,
  body.dark-mode p ,
  body.dark-mode span{
    color: var(--text-color);
  }
  
  /* Add more text elements as needed */