.contact-1 {

    font-family: cursive;
    font-style: italic;
    font-size: 2.5vmin;
    vertical-align: text-bottom;
    width: 90vh;

}

.contact-1 h2 {
    text-align: center;
    margin: 7vmin 0 5vmin 0;
    font-size: 10vmin;
}

.contact-1 .form-label,
.contact-1 .btn {
    font-size: 3.5vmin;
    margin: 1vmin 0 1vmin 0;
}

.contact-1 .btn {
    padding: 1.5vmin;
    display: block;
    margin: 4vmin auto;
    border-style: none;
    background-image: linear-gradient(rgb(237, 229, 229), rgba(63, 52, 52, 0.507));
    /* border-radius: 10px; */
    font-family: cursive;
    font-style: italic;
    font-size: 3vmin;


}

body {
    --text-color: white;
    /* Your other styles */
}

body.dark-mode {
    background-color: #222;
}

body.dark-mode a {
    color: var(--text-color);
}

body.dark-mode h1,
body.dark-mode h2,
body.dark-mode h3,
body.dark-mode h4,
body.dark-mode h5,
body.dark-mode h6,
body.dark-mode p,
body.dark-mode label {
    color: var(--text-color);
}

/* Add more text elements as needed */