.aboutimg {
    height: 90vh;
    width: 80vmin;
    border-radius: 30px;
    border-style: none;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 25px 50px -12px;

}

.about {
  font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 2.5vmin;
    vertical-align: text-bottom;
}

.aboutlist {
    /* border: 1px solid #000; */
    text-align: center;
    padding-block: 0rem;
    padding-inline: 0rem;
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 3vmin;
    color: #092b12;
}

.aboutlist li span {
    padding: 1rem;
    margin-inline: 3rem;
    color: #092b12;
}

body {
    --text-color: white;
    /* Your other styles */
  }
  
  body.dark-mode {
    background-color: #222;
  }
  
  body.dark-mode a {
    color: var(--text-color);
  }
  
  body.dark-mode h1,
  body.dark-mode h2,
  body.dark-mode h3,
  body.dark-mode h4,
  body.dark-mode h5,
  body.dark-mode h6,
  body.dark-mode p ,
  body.dark-mode span{
    color: var(--text-color);
  }
  
