
.projectcardbody{
  font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 3.2vmin;
    margin-inline: 2rem;
    margin-block: 0.5rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-weight: bold;
    color: #092b12;
}
.projectnumber{
  font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 800;    
}
.card-header{
  font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 3vmin;
    text-decoration: underline;
    text-shadow: #092b12;
    text-transform: uppercase;
    /* font-weight: bold; */
    color: #092b12;
}
.card-footer{
  font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 2.5vmin;
    margin-block: 0.5rem;
    font-weight: bold;
    color: #092b12;
    
}

.projectbtn{
    border-style: none;
    background-image: linear-gradient(rgb(237, 229, 229),rgba(63, 52, 52, 0.507));
    color: rgb(201, 225, 245);
    padding: 10px;
    /* border-radius: 10px; */
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 3vmin;
    
}
.extraprojectcard{
    color: #092b12;
}
.extraprojectcard h5{
    font-weight: 700;
    text-decoration: underline;
}


body {
    --text-color: white;
    /* Your other styles */
  }
  
  body.dark-mode ,
  .dark-mode .card-body ,
  .dark-mode .card{
    background-color: #222;
  }

  body.dark-mode a {
    color: var(--text-color);
  }
  
  body.dark-mode h1,
  body.dark-mode h2,
  body.dark-mode h3,
  body.dark-mode h4,
  body.dark-mode h5,
  body.dark-mode h6,
  body.dark-mode p ,
  body.dark-mode span,
  body.dark-mode .card-header,
  body.dark-mode .card-footer{
    color: var(--text-color);
  }

  .dark-mode .btn{
    background-color: white ;
    color: #222;
  }
  
  /* Add more text elements as needed */
